@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800;900&family=Space+Grotesk:wght@700;900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

/* Global App Visibility Improvements */
html {
  /* Reduce base scale by ~15% (from 18px -> 15.3px) */
  font-size: 15.3px;
}

body {
  font-size: 1.1rem;
  line-height: 1.6;
  transition: background-color 0.3s cubic-bezier(0.4, 0, 0.2, 1), color 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Enhanced Typography Scale with Better Line Heights */
.text-sm { font-size: 0.813rem; line-height: 1.5; }  /* 12.45px */
.text-base { font-size: 0.938rem; line-height: 1.6; } /* 14.35px */
.text-lg { font-size: 1.063rem; line-height: 1.6; }   /* 16.26px */
.text-xl { font-size: 1.25rem; line-height: 1.5; }    /* 19.13px */
.text-2xl { font-size: 1.563rem; line-height: 1.4; }  /* 23.91px */
.text-3xl { font-size: 1.953rem; line-height: 1.3; }  /* 29.88px */
.text-4xl { font-size: 2.441rem; line-height: 1.2; }  /* 37.35px */
.text-5xl { font-size: 3.052rem; line-height: 1.1; }  /* 46.69px */
.text-6xl { font-size: 3.815rem; line-height: 1.1; }  /* 58.37px */

/* GitHub contribution graph grid */
.grid-cols-53 {
  grid-template-columns: repeat(53, minmax(0, 1fr));
}

/* Ensure mobile sections have proper spacing */
@media (max-width: 1023px) {
  #about-mobile,
  #music,
  #favorite-movies {
    margin-bottom: 2rem;
  }
}

/* Liquid Glass Effect - Enhanced */
.liquid-bg {
  position: relative;
  overflow: hidden;
}

.liquid-bg::before,
.liquid-bg::after {
  content: '';
  position: absolute;
  width: 400px;
  height: 400px;
  background: linear-gradient(
    45deg,
    rgba(59, 130, 246, 0.15),
    rgba(147, 51, 234, 0.15),
    rgba(236, 72, 153, 0.15),
    rgba(59, 130, 246, 0.15)
  );
  border-radius: 50%;
  filter: blur(60px);
  opacity: 0.4;
  animation: liquid-float 25s ease-in-out infinite;
  z-index: -1;
}

.liquid-bg::before {
  top: -200px;
  left: -200px;
  animation-delay: 0s;
}

.liquid-bg::after {
  bottom: -200px;
  right: -200px;
  animation-delay: -12.5s;
}

@keyframes liquid-float {
  0%, 100% {
    transform: translate(0, 0) rotate(0deg) scale(1);
    opacity: 0.4;
  }
  25% {
    transform: translate(-30px, -30px) rotate(90deg) scale(1.1);
    opacity: 0.5;
  }
  50% {
    transform: translate(-20px, 20px) rotate(180deg) scale(0.95);
    opacity: 0.3;
  }
  75% {
    transform: translate(30px, -20px) rotate(270deg) scale(1.05);
    opacity: 0.45;
  }
}

/* Liquid Button Effect */
.liquid-button {
  position: relative;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.7));
  border: 1px solid rgba(255, 255, 255, 0.3);
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.liquid-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  transition: left 0.5s;
}

.liquid-button:hover::before {
  left: 100%;
}

.liquid-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

/* Liquid Wave Effect */
.liquid-wave {
  position: relative;
  overflow: hidden;
}

.liquid-wave::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 30%, rgba(255, 255, 255, 0.1) 50%, transparent 70%);
  transform: translateX(-100%);
  animation: wave 3s ease-in-out infinite;
  z-index: 1;
}

@keyframes wave {
  0% {
    transform: translateX(-100%);
  }
  50% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(100%);
  }
}

/* Scroll offset for anchor links */
.scroll-offset {
  scroll-margin-top: 2rem;
}

/* Line clamp utilities */
.line-clamp-1 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}

.line-clamp-3 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* Focus styles for accessibility */
*:focus {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

/* Smooth transitions */
* {
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

/* Hover effects */
.hover\:scale-105:hover {
  transform: scale(1.05);
}

.hover\:scale-110:hover {
  transform: scale(1.1);
}

.hover\:scale-125:hover {
  transform: scale(1.25);
}

.hover\:-translate-y-1:hover {
  transform: translateY(-0.25rem);
}

.hover\:-translate-y-2:hover {
  transform: translateY(-0.5rem);
}

.hover\:rotate-6:hover {
  transform: rotate(6deg);
}

.hover\:rotate-12:hover {
  transform: rotate(12deg);
}

.hover\:rotate-180:hover {
  transform: rotate(180deg);
}

/* Animation utilities */
.animate-pulse {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: .5;
  }
}

.animate-spin {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Fade-in animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.animate-fade-in {
  animation: fadeIn 0.6s ease-out forwards;
}

.animate-fade-in-up {
  animation: fadeInUp 0.8s cubic-bezier(0.16, 1, 0.3, 1) forwards;
}

/* Initial hidden state */
.opacity-0-init {
  opacity: 0;
}

/* Stagger delay utilities */
.delay-0 { animation-delay: 0ms; }
.delay-100 { animation-delay: 100ms; }
.delay-200 { animation-delay: 200ms; }
.delay-300 { animation-delay: 300ms; }
.delay-400 { animation-delay: 400ms; }
.delay-500 { animation-delay: 500ms; }
.delay-600 { animation-delay: 600ms; }
.delay-700 { animation-delay: 700ms; }
.delay-1000 { animation-delay: 1000ms; }

/* Hover lift effect */
.hover-lift {
  transition: all 0.3s cubic-bezier(0.16, 1, 0.3, 1);
}

.hover-lift:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px -5px rgba(0, 0, 0, 0.15);
}

.dark .hover-lift:hover {
  box-shadow: 0 10px 30px -5px rgba(0, 0, 0, 0.4);
}

/* Hover glow effect */
.hover-glow {
  transition: all 0.3s cubic-bezier(0.16, 1, 0.3, 1);
}

.hover-glow:hover {
  box-shadow: 0 0 20px rgba(59, 130, 246, 0.3);
}

.dark .hover-glow:hover {
  box-shadow: 0 0 25px rgba(96, 165, 250, 0.4);
}

/* Gradient overlay effect */
.gradient-overlay {
  position: relative;
  overflow: hidden;
}

.gradient-overlay::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.05) 0%, rgba(147, 51, 234, 0.05) 100%);
  opacity: 0;
  transition: opacity 0.5s cubic-bezier(0.16, 1, 0.3, 1);
  pointer-events: none;
  z-index: 1;
}

.gradient-overlay:hover::before {
  opacity: 1;
}

/* Glass-morphism effect */
.glass-card {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(12px) saturate(180%);
  -webkit-backdrop-filter: blur(12px) saturate(180%);
  border: 1px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.08);
}

.dark .glass-card {
  background: rgba(30, 41, 59, 0.7);
  border: 1px solid rgba(148, 163, 184, 0.2);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.4);
}

.glass-card-hover {
  transition: all 0.5s cubic-bezier(0.16, 1, 0.3, 1);
}

.glass-card-hover:hover {
  background: rgba(255, 255, 255, 0.85);
  backdrop-filter: blur(16px) saturate(200%);
  -webkit-backdrop-filter: blur(16px) saturate(200%);
  border: 1px solid rgba(255, 255, 255, 0.5);
  box-shadow: 0 12px 48px 0 rgba(0, 0, 0, 0.12);
}

.dark .glass-card-hover:hover {
  background: rgba(30, 41, 59, 0.85);
  border: 1px solid rgba(148, 163, 184, 0.3);
  box-shadow: 0 12px 48px 0 rgba(0, 0, 0, 0.6);
}

/* GPU acceleration for better performance */
.will-change-transform {
  will-change: transform, opacity;
  transform: translateZ(0);
  backface-visibility: hidden;
  perspective: 1000px;
}

/* Respect reduced motion preference */
@media (prefers-reduced-motion: reduce) {
  .animate-fade-in,
  .animate-fade-in-up {
    animation: none;
    opacity: 1;
    transform: none;
  }

  *, *::before, *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Dark mode styles */
.dark {
  @apply bg-slate-900 text-white;
}

.dark .bg-white {
  @apply bg-slate-800;
}

.dark .text-black {
  @apply text-white;
}

.dark .text-gray-800 {
  @apply text-slate-200;
}

.dark .text-gray-600 {
  @apply text-slate-400;
}

.dark .text-gray-700 {
  @apply text-slate-300;
}

.dark .text-gray-500 {
  @apply text-slate-400;
}

.dark .text-gray-400 {
  @apply text-slate-500;
}

.dark .border-gray-200 {
  @apply border-slate-700;
}

.dark .border-gray-300 {
  @apply border-slate-600;
}

.dark .bg-gray-50 {
  @apply bg-slate-800;
}

.dark .bg-gray-100 {
  @apply bg-slate-700;
}

.dark .bg-gray-200 {
  @apply bg-slate-600;
}

.dark .bg-gray-800 {
  @apply bg-slate-700;
}

.dark .hover\:bg-gray-50:hover {
  @apply hover:bg-slate-800;
}

.dark .hover\:bg-gray-100:hover {
  @apply hover:bg-slate-700;
}

.dark .hover\:text-gray-600:hover {
  @apply hover:text-slate-300;
}

.dark .hover\:text-gray-900:hover {
  @apply hover:text-slate-100;
}

.dark .hover\:border-gray-300:hover {
  @apply hover:border-slate-600;
}

/* Dark mode scrollbar */
.dark ::-webkit-scrollbar-track {
  @apply bg-slate-800;
}

.dark ::-webkit-scrollbar-thumb {
  @apply bg-slate-600;
}

.dark ::-webkit-scrollbar-thumb:hover {
  @apply bg-slate-500;
}

/* Dark mode focus styles */
.dark *:focus {
  outline-color: #94a3b8;
}

/* Dark mode shadows */
.dark .shadow-sm {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.3);
}

.dark .shadow {
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.3), 0 1px 2px 0 rgba(0, 0, 0, 0.2);
}

.dark .shadow-md {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.3), 0 2px 4px -1px rgba(0, 0, 0, 0.2);
}

.dark .shadow-lg {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.3), 0 4px 6px -2px rgba(0, 0, 0, 0.2);
}

.dark .shadow-xl {
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.3), 0 10px 10px -5px rgba(0, 0, 0, 0.2);
}

.dark .shadow-2xl {
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.4);
}

/* Dark mode hover shadow effects */
.dark .hover\:shadow-xl:hover {
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.3), 0 10px 10px -5px rgba(0, 0, 0, 0.2);
}

.dark .hover\:shadow-2xl:hover {
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.4);
}

/* Dark mode liquid glass effect adjustments */
.dark .liquid-bg::before,
.dark .liquid-bg::after {
  opacity: 0.25;
}

.dark .liquid-button {
  background: linear-gradient(135deg, rgba(30, 41, 59, 0.9), rgba(30, 41, 59, 0.7));
  border: 1px solid rgba(148, 163, 184, 0.2);
}

.dark .liquid-button::before {
  background: linear-gradient(90deg, transparent, rgba(148, 163, 184, 0.2), transparent);
}

/* ========================================
   3D Scene Styles
   ======================================== */

/* Ensure canvas fills the screen */
html, body, #root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

/* Hide scrollbar for 3D experience */
body {
  overflow: hidden;
}

/* Canvas container */
canvas {
  display: block;
  width: 100% !important;
  height: 100% !important;
}

/* Smooth scroll snap for section navigation */
.scroll-snap-y {
  scroll-snap-type: y mandatory;
}

.scroll-snap-start {
  scroll-snap-align: start;
}

/* Panel animations */
@keyframes slideInFromLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideOutToLeft {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(-30px);
  }
}

.animate-slide-in {
  animation: slideInFromLeft 0.5s cubic-bezier(0.16, 1, 0.3, 1) forwards;
}

.animate-slide-out {
  animation: slideOutToLeft 0.3s cubic-bezier(0.16, 1, 0.3, 1) forwards;
}

/* Bounce animation for scroll hint */
@keyframes bounce {
  0%, 100% {
    transform: translateY(0) translateX(-50%);
  }
  50% {
    transform: translateY(-10px) translateX(-50%);
  }
}

.animate-bounce {
  animation: bounce 2s ease-in-out infinite;
}

/* Navigation dots styles */
.nav-dot {
  transition: all 0.3s cubic-bezier(0.16, 1, 0.3, 1);
}

.nav-dot:hover {
  transform: scale(1.2);
}

.nav-dot.active {
  transform: scale(1.3);
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

/* Loading screen animations */
@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
}

.animate-float {
  animation: float 2s ease-in-out infinite;
}

/* Ghibli-inspired soft glow effect */
.ghibli-glow {
  box-shadow:
    0 0 20px rgba(255, 250, 240, 0.3),
    0 0 40px rgba(255, 228, 181, 0.2),
    0 0 60px rgba(135, 206, 235, 0.1);
}

/* Warm atmospheric overlay */
.warm-overlay {
  background: linear-gradient(
    180deg,
    rgba(255, 228, 181, 0.1) 0%,
    transparent 50%,
    rgba(135, 206, 235, 0.05) 100%
  );
  pointer-events: none;
}

/* Text shadow for better readability over 3D scene */
.text-shadow {
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.3);
}

/* Panel glass effect for 3D overlays */
.panel-glass {
  background: rgba(255, 255, 255, 0.85);
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  border: 1px solid rgba(255, 255, 255, 0.3);
  box-shadow:
    0 8px 32px rgba(0, 0, 0, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.5);
}

.dark .panel-glass {
  background: rgba(30, 41, 59, 0.9);
  border: 1px solid rgba(148, 163, 184, 0.2);
  box-shadow:
    0 8px 32px rgba(0, 0, 0, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.05);
}

/* Line clamp for project descriptions */
.line-clamp-2 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}

/* Prevent text selection on UI elements during navigation */
.no-select {
  user-select: none;
  -webkit-user-select: none;
}

/* Ensure proper z-indexing */
.z-scene { z-index: 0; }
.z-ui { z-index: 40; }
.z-nav { z-index: 50; }
.z-loading { z-index: 100; }

